html {
  background-color: #FEFFF9;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  margin: 1rem 3rem;
  color: #6B9864;
}

.good-job {
  font-family: 'Abril Fatface', cursive;
  font-size: 3rem;
  display: block;
  margin: 3rem 0;
  color: #8659F6;
  letter-spacing: 4px;
}

.total-hours {
  border-bottom: 3px solid #6B9864;
}

.uppercase {
  text-transform: uppercase;
}

.bold {
  font-weight: 700;
}

ul {
  list-style: none;
  line-height: 2.5;
  border-left: 3px solid #8659F6;
  margin-left: 2rem;
  margin-bottom: 3rem;
  padding-left: 1.5rem;
}

.title {
  color: #6B9864;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.date {
  letter-spacing: 2px;
  font-weight: bold;
  text-transform: uppercase;
  color: #8659F6;
  padding: 0 2rem;
}

.button {
  color: white;
  background-color: #8659F6;
  padding: .75rem 2rem;
  text-align: center;
  text-decoration: none;
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 2px;
  border: none;
  cursor: pointer;
}

.float-right {
  float: right;
}

.date-btn {
  cursor: pointer;
  color: #8659F6;
  font-weight: 700;
}

.settings-btn {
  color: #FFFFFF;
  background-color: #AAAAAA;
  padding: .75rem 1rem;
  text-align: center;
  text-decoration: none;
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 2px;
  border: none;
  cursor: pointer;
  margin-right: 1rem;
}
